<template>
  <div v-if="Object.keys(goods).length!== 0" class="baseInfo">
    <p class="desc">{{ goods.desc }}</p>

    <div class="priceInfo">
      <span class="price">{{ '￥' + goods.highPrice }}</span>
      <span class="high-price" v-if="highPrice">{{ '￥' + highPrice }}</span>
      <span class="discount">{{ goods.priceInfo }}</span>
    </div>

    <div class="sale">
      <span class="sales">{{ '销量 ' + goods.sales }}</span>
      <span class="cFav">{{ '收藏 ' + goods.cFav + '人' }}</span>
      <span class="express">{{ goods.express }}</span>
    </div>

    <div class="services">
      <span class="service1">{{ service1 }}</span>
      <span class="service2">{{ service2 }}</span>
    </div>

  </div>
</template>

<script>
export default {
  name: "DetailBaseInfo",
  props: {
    goods: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed: {
    highPrice() {
      return this.goods.priceInfo &&
        (this.goods.highPrice / parseInt(this.goods.priceInfo.slice(0, 1)) * 10).toFixed(2);
    },
    service1() {
      return this.goods.services && this.goods.services[1].name;
    },
    service2() {
      return this.goods.services && this.goods.services[0].name;
    }
  }
}
</script>

