export class Specification {
  /**
   * @param skus 所有的颜色 和 尺码的排列组合。 []
   * @param colorAndSizeList 包含所有的颜色 和 所有的尺码
   * @param colorList 仅仅包含所有的颜色，以及颜色对应的图片
   */
  constructor(skus, colorAndSizeList, colorList) {
    this.skus = skus;
    //this.props = colorAndSizeList;
    //this.colorList = colorList;

    if (colorAndSizeList[0].label === '颜色') {
      this.colors = colorAndSizeList[0].list;
    }
    if (colorAndSizeList[1].label === '尺码') {
      this.sizes = colorAndSizeList[1].list;
    }
    // 找到图片
    for (let i = 0; i < colorList.length; i++) {
      for (let j = 0; j < this.colors.length; j++) {
        if (colorList[i].label === this.colors[j].name) {
          this.colors[j].image = colorList[i].image;
          this.colors[j].stockId = colorList[i].stockId;
        }
      }
    }
    // 统计库存量-对应的颜色
    for (let i = 0; i < this.colors.length; i++) {
      let count = 0;
      for (let j = 0; j < this.skus.length; j++) {
        if (this.colors[i].styleId === this.skus[j].styleId) {
          count += this.skus[j].stock;
        }
      }
      this.colors[i].stock = count;
    }

    // 统计库存量-对应的颜色
    for (let i = 0; i < this.sizes.length; i++) {
      let count = 0;
      for (let j = 0; j < this.skus.length; j++) {
        if (this.sizes[i].sizeId === this.skus[j].sizeId) {
          count += this.skus[j].stock;
        }
      }
      this.sizes[i].stock = count;
    }

    // 计算中的库存量
    let count = 0;
    for (let i = 0; i < this.colors.length; i++) {
      // 计算所有的库存
      count += this.colors[i].stock;
    }
    this.totalStock = count;
  }
}


