<template>
  <nav-bar nav-bg="#f1f1f1">
    <img slot="left" src="~@/assets/img/common/back.svg" alt=""
    @click="backClick">
    <div slot="center" class="nav-center">
      <!-- 点击顶部标题,滚动到指定的主题-->
      <span v-for="(item,index) in titles"
            :class="{current: currentIndex=== index}"
      @click="titleClick(index)">{{ item }}</span>
    </div>
  </nav-bar>
</template>

<script>
import NavBar from "@/components/common/navbar/NavBar";

export default {
  name: "DetailNavBar",
  components: {
    NavBar
  },
  data(){
    return{
      currentIndex :0
    }
  },
  props: {
    index: {
      type: Number,
      default: 0
    },
    titles: {
      type: Array,
      default() {
        return ['aa', 'bb', 'cc', 'dd']
      }
    }
  },
  methods:{
    titleClick(index){
      this.currentIndex = index;
      //console.log('titleClick--',index);
      this.$emit('titleClick',index);
    },
    backClick(){
      this.$router.back();
    }
  }
}
</script>

<style scoped>
.nav-center {
  display: flex;
}

.nav-center span {
  flex: 1;
  color: var(--color-text);
}

.nav-center span.current {
  color: var(--color-high-text);
}

</style>
