<template>
  <div class="detail">
    <!--顶部导航-->
    <detail-nav-bar :titles="titles" :index="currentIndex"
                    ref="navbar"
                    @titleClick="titleClick"/>

    <scroll class="content"
            ref="scroll"
            @scroll="contentScroll"
            :probe-type="3">
      <!--轮播图-->
      <detail-swiper :top-imgs="swiperImgs"/>
      <!--商品信息-->
      <detail-base-info :goods="goods"/>
      <!--商品商家信息-->
      <detail-shop-into v-bind:shop-info="shopInfo"/>
      <!-- 商品推荐-->
      <detail-shop-recommend :recommend-data="shopRecommend"/>
      <!-- 商品评论信息的展示-->
      <detail-rate ref="comment" :rate-info="rateInfo"/>
      <!--该商品的详情数据-->
      <detail-goods-info ref="detail" :detail-info="detailInfo" @imgLoad="imgLoad"/>
      <!-- 商品推荐 24个-->
      <detail-bottom-recommend ref="recommend" :recommend-data="goodsRecommend.list"/>
    </scroll>

    <detail-bottom-bar @addCart="addCart"/>
    <!-- @click.native修饰符，可以获取自定义组件的事件-->
    <back-top @click.native="backClick" v-show="isShowBackTop" ref="bckTop"/>

    <!-- 购物车选择界面 展示库存、价格、颜色、尺码、选择的数量-->
    <detail-speci ref="detailSpeci" :specification="specification"
                  :now-price="goods.highPrice"
                  :title="goods.desc"
                  :goodsId="id"
                  :shopId="shopInfo.shopId"
                  :shopName="shopInfo.shopName"
                  @submit="submit"/>
  </div>
</template>

<script>
import DetailNavBar from "@/views/detail/detailChild/DetailNavBar";
import DetailSwiper from "@/views/detail/detailChild/DetailSwiper";
import DetailBaseInfo from "@/views/detail/detailChild/DetailBaseInfo";
import DetailShopInto from "@/views/detail/detailChild/DetailShopInfo";
import DetailGoodsInfo from "@/views/detail/detailChild/DetailGoodsInfo";
import DetailRate from "@/views/detail/detailChild/DetailRate";
import DetailShopInfo from "@/views/detail/detailChild/DetailShopInfo";
import DetailBottomBar from "@/views/detail/detailChild/DetailBottomBar";
import DetailShopRecommend from "@/views/detail/detailChild/DetailShopRecommend";
import DetailBottomRecommend from "@/views/detail/detailChild/DetailBottomRecommend";
import DetailSpeci from "@/views/detail/detailChild/DetailSpeci";

import Scroll from "@/components/common/scroll/Scroll";
import {getDetailRecommend, getDetailData, Goods, ShopInfo, Specification} from "@/network/Detail";
import {debounce} from "@/common/utils";
import {backTopMixin} from "@/common/mixin";
import {ADD_TO_CART} from "@/store/mutations-types";

// 映射actions方法，到vue实例里面
import {mapActions} from 'vuex'

export default {
  name: "Detail",
  components: {
    DetailSpeci,
    DetailBottomBar,
    DetailBottomRecommend,
    DetailShopRecommend,
    Scroll,
    DetailGoodsInfo,
    DetailShopInto,
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailRate,
    DetailShopInfo,

  },
  // 使用混入，一定要在这里初始化
  mixins: [backTopMixin],
  data() {
    return {
      titles: ['商品', '评论', '参数', '推荐'],
      currentIndex: 0,
      id: '',
      // 顶部的轮播图数组
      swiperImgs: [],

      //该商品信息 价格、折扣、销量、服务等等
      goods: {},

      //店铺信息
      shopInfo: {},

      //详情信息
      detailInfo: {},

      // 店铺下方的物品推荐
      shopRecommend: null,

      // 评价数据
      rateInfo: null,

      // 详情页面最底部的商品推荐 24个
      goodsRecommend: {},

      //详细规格，颜色 尺码 库存
      specification: {},

      //防抖处理后的函数 this.$refs.scroll.refreshScroll
      refresh: null,

      // 所有主题的offsetTop的位置
      themeTopYs: [],

      //通过防抖函数封装的方法
      getThemeTopY: null,
      isShowToast: false,
    }
  },
  methods: {
    ...mapActions({
      // 最终提交到vuex中的方法
      addToCart: ADD_TO_CART
    }),//映射为methods
    getDetailRecommend(id) {
      getDetailRecommend(id).then(value => {
        console.log('recommend==>', value);
        // 猜你喜欢
        this.goodsRecommend = value.data.wall;
      })
    },
    getDetailData(id) {
      getDetailData(id).then(value => {
        const result = value.data.result;
        console.log('getDetailData', this.id, result);

        //数据获取成功，进行剥离数据，塞到要展示的对象中
        this.swiperImgs = result.topImages;

        //头部的标题--商品信息
        this.goods = new Goods(result.itemInfo, result.itemServices, result.normalPrice);

        //商家信息
        this.shopInfo = new ShopInfo(result.shopInfo);

        this.detailInfo = result.detailInfo;
        //console.log('this.shopInfo=-=>', this.detailInfo);

        this.rateInfo = result.rateInfoV2; // 评价信息

        //店铺底部三个商品推荐
        this.shopRecommend = result.shopRecommend.realTimeRecommendItemList;

        //result.skuInfo;//颜色规格参数 .skus所有的排列组合[]  .props 所有的 尺码和颜色分开的[]
        this.specification = new Specification(result.skuInfo.skus, result.skuInfo.props, result.skuBarInfo.list);

        // 获取到最新的数据，对应的dom已经被加载出来了。但是内容还没有被填充。比如图片数据
        // ==> 还是可能会因为图片的加载过慢，导致计算错误。=>解决：当图片加载完成时，调用一次
        /*this.$nextTick(()=>{
          this.themeTopYs = [];
          this.themeTopYs.push(0);
          this.themeTopYs.push(-(this.$refs.comment.$el.offsetTop));
          this.themeTopYs.push(-(this.$refs.detail.$el.offsetTop));
          this.themeTopYs.push(-(this.$refs.recommend.$el.offsetTop));
          console.log(this.themeTopYs)
        })*/

      }, error => {
        console.log('getDetailData', this.id, error);
      })

    },
    imgLoad() {
      this.refresh();
      /*  // ==>这种方法会被调用多次，下面使用防抖函数进行处理
      // 图片有多少就会被调用多少
      this.$nextTick(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(-(this.$refs.comment.$el.offsetTop));
      this.themeTopYs.push(-(this.$refs.detail.$el.offsetTop));
      this.themeTopYs.push(-(this.$refs.recommend.$el.offsetTop));
      console.log(this.themeTopYs)
    });*/

      // 使用通过防抖函数处理过的函数 现在只会被调用一次
      this.getThemeTopY();
    },
    // 滚动监听
    contentScroll(position) {
      //console.log('contentScroll',position.y);
      const positionY = position.y;
      // 混入函数中，设置是否显示返回顶部按钮
      this.isShowBackTop = positionY < -1000;
      if (this.themeTopYs[0] >= positionY && this.themeTopYs[1] < positionY) {
        this.currentIndex = 0;
      } else if (this.themeTopYs[1] >= positionY && this.themeTopYs[2] < positionY) {
        this.currentIndex = 1;
      } else if (this.themeTopYs[2] >= positionY && this.themeTopYs[3] < positionY) {
        this.currentIndex = 2;
      } else {
        this.currentIndex = 3;
      }
      this.$refs.navbar.currentIndex = this.currentIndex;
    },
    // 根据监听到的index，滚动到对应的主题
    titleClick(index) {
      //console.log('titleClick', index);
      // 滚动到具体的位置   [0, -936, -1122, -5558]
      this.$refs.scroll.scroll.scrollTo(0, this.themeTopYs[index], 300);
    },
    // 获取商品信息，展示商品型号，将数据添加到vuex中。模拟添加购物车的过程
    addCart() {
      //console.log('addCart1');
      this.$refs.detailSpeci.isShow = true;
    },
    submit(selectedItem) {
      //console.log('submit==>', selectedItem);
      // 组织要保存到vuex中的数据 商家信息、item信息(图片、标题、价格、选择的颜色规格数量等信息)
      //this.$store.commit('addCart',selectedItem);//通过mutations直接修改
      // 使用actions提交数据修改

      /*this.$store.dispatch(ADD_TO_CART, selectedItem).then(value => {
        // 3.添加到购物车成功 ==> 弹出toast。 整个过程执行结束，才会执行toast
        console.log(value);
      })*/
      this.addToCart(selectedItem).then(value => {
        // 3.添加到购物车成功 ==> 弹出toast。 整个过程执行结束，才会执行toast
        console.log(value);
        this.$toast.show('添加购物车成功！');
        this.$router.push('/cart')
      })
    }
  },
  created() {
    //console.log('detail--created');
    this.id = this.$route.params.id;
    //console.log('this.$route.params', this.$route.params);
    // 给方法赋值(通过防抖函数包裹后的)
    this.getThemeTopY = debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(-(this.$refs.comment.$el.offsetTop));
      this.themeTopYs.push(-(this.$refs.detail.$el.offsetTop));
      this.themeTopYs.push(-(this.$refs.recommend.$el.offsetTop));
      //console.log(this.themeTopYs)
    }, 100)
  },
  activated() {
    //console.log('detail--activated', this.id);
    //发送网络请求，获取商品详情数据
    this.getDetailData(this.id);

    // 发送请求，获取相关商品推荐 24个
    this.getDetailRecommend(this.id);


  },
  mounted() {
    // 将需要防抖操作的方法，包装一下。只有两次调用的方法间隔大于1S，才会执行一次！
    this.refresh = debounce(this.$refs.scroll.refreshScroll, 1000);

  },
  updated() {
    // update方法会被调用多次，下面的代码会被多次重复执行。 -=> 使用 this.$nextTick( () =>{} );
    //this.themeTopYs = [];
    //this.themeTopYs.push(0);
    //this.themeTopYs.push(-(this.$refs.comment.$el.offsetTop));
    //this.themeTopYs.push(-(this.$refs.detail.$el.offsetTop));
    //this.themeTopYs.push(-(this.$refs.recommend.$el.offsetTop));
    //console.log(this.themeTopYs)

  }
}
</script>

<style scoped>

.content {
  position: absolute;
  top: 44px;
  bottom: 50px;
}
</style>
