<template>
  <div class="detail-speci" v-if="Object.keys(specification).length!==0 && isShow">
    <div class="speci">
      <!-- 信息栏-->
      <div class="info">
        <!-- 图片-->
        <div class="img">
          <img :src="image" alt="">
        </div>
        <!-- 商品信息-->
        <div class="goods">
          <div class="price">￥{{ nowPrice }}</div>
          <div class="stock">库存：{{ stockText }} 件</div>
          <div class="type">
            <span class="selected" v-if="selectedColor!==selectedSize">
              <span>已选：</span>
              <span class="select-content">
                {{ selectedColor | formatText }} {{ selectedSize | formatText }}
              </span>
            </span>
            <span class="tip" v-if="isShowTip">{{ tipText }}</span>
          </div>
        </div>
        <!-- 右上角的关闭-->
        <div class="close" v-on:click="hideDialog">╳</div>
      </div>
      <!-- 颜色款式-->
      <div class="colors select">
        <div class="title">颜色</div>
        <div class="text">
          <span v-for="(item,index) in specification.colors" :key="index"
                @click="selectColor(item)" :style="colorSpanStyle(item)">
            {{ item.name }}
          </span>
        </div>
      </div>
      <!-- 尺码大小-->
      <div class="sizes select">
        <div class="title">尺码</div>
        <div class="text">
          <span v-for="(item,index) in specification.sizes" :key="index"
                @click="selectSize(item)" :style="sizeSpanStyle(item)">
            {{ item.name }}
          </span>
        </div>
      </div>
      <!--选中的数量-->
      <div class="count select">
        <div class="title">数量</div>
        <div class="counter">
          <span @click="subCount" :style="subBtnStyle">-</span>
          <input type="number" value="1" v-model="count">
          <span @click="addCount">+</span>
        </div>
      </div>
      <!--确认按钮-->
      <div class="submit" @click="submit">确认</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailSpeci",
  data() {
    return {
      isShow: false,
      // 默认选中第一个颜色款式
      currentIndex: 0,
      tipText: '请选择 颜色 尺码',
      selectedColor: '',
      selectedSize: '',
      selectedItem: {},//存放颜色 和 尺寸子项
      // 默认不选中的值为 -1
      selectedColorId: -1,
      selectedSizeId: -1,
      nowStock: 0,
      count: 1
    }
  },
  props: {
    specification: {},
    nowPrice: 0,
    title: '',
    goodsId: 0,
    shopId:0,
    shopName:0
  },
  filters: {
    formatText(text) {
      if (text === '') {
        return '';
      }
      return '"' + text + '"';
    }
  },
  computed: {
    stockText() {
      // 颜色和尺码都已经选择了
      if (this.selectedColorId !== -1 || this.selectedSizeId !== -1) {
        return this.nowStock;
      } else {
        return this.specification.totalStock;
      }
    },
    // 返回当前选择的图片
    image() {
      return this.specification.colors[this.currentIndex].image;
    },
    colorSpanStyle() {
      return function (item) {
        if (item.name === this.selectedColor) {
          return {color: '#ff5777', borderColor: '#ff5777'};
        } else {
          return {};
        }
      }
    },
    sizeSpanStyle() {
      return function (item) {
        if (item.name === this.selectedSize) {
          return {color: '#ff5777', borderColor: '#ff5777'};
        } else {
          return {};
        }
      }
    },
    isShowTip() {
      return this.selectedColor === '' || this.selectedSize === '';
    },
    subBtnStyle() {
      if (this.count === 1) {
        return {backgroundColor: '#f6f6f6', color: '#bfbfbf'}
      } else {
        return {}
      }
    }

  },
  methods: {
    hideDialog() {
      this.isShow = false;
    },
    findStock(item) {
      //console.log('findStock==>', item);
      // true:表示选中某一款式颜色,没有选择尺码的剩余库存=> 计算该种颜色的剩余库存
      if (this.selectedColorId !== -1 && this.selectedSizeId === -1) {
        //console.log('计算该种颜色的剩余库存');
        this.nowStock = item.stock;
      }
      // true:计算该尺码的剩余库存
      else if (this.selectedColorId === -1 && this.selectedSizeId !== -1) {
        //console.log('计算该尺码的剩余库存');
        this.nowStock = item.stock;
      } else {
        //console.log('计算尺码加颜色，剩余库存');
        // 从所有的排列组合中，进行筛选
        for (let i = 0; i < this.specification.skus.length; i++) {
          if (this.selectedColorId === this.specification.skus[i].styleId
            && this.selectedSizeId === this.specification.skus[i].sizeId) {
            this.nowStock = this.specification.skus[i].stock;
          }
        }
      }
    },
    selectColor(item) {
      //console.log('selectColor==>',item);
      // 颜色取消
      if (this.selectedColor === item.name) {
        this.selectedColor = '';
        this.selectedColorId = -1;
        this.tipText = '，请选择 颜色';
      } else {// 颜色选中
        this.selectedColorId = item.styleId;
        this.selectedColor = item.name;
        this.tipText = '，请选择 尺码';
      }
      if (this.selectedColor === this.selectedSize) {
        this.tipText = '请选择 颜色 尺码'
      }
      // 修改当前选中的位置，为了修改图片做准备
      this.currentIndex = item.index - 1;
      // 点击后更新剩余库存量
      this.findStock(item);
      this.selectedItem.color = item;
    },
    selectSize(item) {
      //console.log('selectSize==>',item);
      if (this.selectedSize === item.name) {
        this.selectedSize = '';
        this.tipText = '，请选择 尺码';
        // 值为-1 代表没有选中尺码span
        this.selectedSizeId = -1;
      } else {
        this.selectedSize = item.name;
        this.tipText = '，请选择 颜色';
        // 100 101 102
        this.selectedSizeId = item.sizeId;
      }
      if (this.selectedColor === this.selectedSize) {
        this.tipText = '请选择 颜色 尺码'
      }
      //点击后更新剩余库存量
      this.findStock(item);
      this.selectedItem.size = item;

    },
    subCount() {
      if (this.count === 1) {
        return;
      }
      this.count--;

    },
    addCount() {
      this.count++;
    },
    submit() {
      this.selectedItem.image = this.image;
      this.selectedItem.count = this.count;
      this.selectedItem.price = this.nowPrice;
      this.selectedItem.title = this.title;
      this.selectedItem.goodsId = this.goodsId;
      this.selectedItem.shopId = this.shopId;
      this.selectedItem.shopName = this.shopName;
      this.selectedItem.isChecked = false;

      let product ={};
      product =JSON.parse(JSON.stringify(this.selectedItem));
      // 发出提交购物车的请求
      this.$emit('submit',product);
      this.hideDialog();

    }
  }
}
</script>

