<template>
  <div class="detail-rate" v-if="rateInfo!==null&&Object.keys(rateInfo).length!==0" >
    <!-- 标题文字-->
    <div class="title">
      <h4>评价({{ cRate }})</h4>
      <div class="score">
        <b>评分</b>
        <span>{{ cScore }}</span>
      </div>
    </div>
    <!-- 评论的标签-->
    <div class="tags">
      <!-- for循环遍历传入的数据-->
      <div class="tag" v-for="item in rateTags">{{ item.value }}({{ item.num }})</div>
    </div>
    <!--具体的评价信息-->
    <div class="content">
      <!-- 评论-->
      <div class="comment">
        <div class="item">
          <!-- 用户信息-->
          <div class="user">
            <div class="avatar">
              <img :src="list.user.avatar" alt="">
            </div>
            <div class="info">
              <span class="name">{{ list.user.uname }}</span>
              <span class="data">{{ list.created|getData }}</span>
            </div>
          </div>
          <!-- 评论内容-->
          <p>{{ list.content }}</p>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "DetailRate",
  props: {
    rateInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  filters: {
    getData(date) {
      return new Date(date * 1000).Format('yyyy-MM-dd hh:mm:ss');
    }
  },
  computed: {
    cRate() {
      return this.rateInfo && this.rateInfo.cRate;
    },
    cScore() {
      return this.rateInfo && this.rateInfo.cScore;
    },
    rateTags() {
      // 只显示前三个tag
      return this.rateInfo && this.rateInfo.rateTags.splice(0, 3);
    },
    list() {
      //TODO:待做，水平展示5条评论，可显示区域只有一条，通过手向左滑动，显示下一条评论
      //return this.rateInfo && this.rateInfo.list.slice(0, 1);
      // 只展示第一条评论信息
      return this.rateInfo && this.rateInfo.list && this.rateInfo.list[0];
    }
  }
}
</script>

